export default [
  {
    path: '/',
    redirect: 'collection'
  },
  {
    path: '/mainPage',
    name: 'mainPage',
    meta: {
      isMenu: true,
      title: '主页',
      icon: 'icon-xitonghanshu'
    },
    component: (resolve) => require(['../view/mainPage/mainPage.vue'], resolve),
    children: [
      {
        path: '/collection',
        name: 'collection',
        meta: {
          isMenu: true,
          title: '主页',
          icon: 'icon-xitonghanshu'
        },
        component: (resolve) => require(['../view/collection/collection.vue'], resolve)
      },
      {
        path: '/works',
        name: 'works',
        meta: {
          isMenu: true,
          title: '作品',
          icon: 'icon-xitonghanshu'
        },
        component: (resolve) => require(['../view/works/works.vue'], resolve)
      },
      {
        path: '/introduction',
        name: 'introduction',
        meta: {
          isMenu: true,
          title: '个人介绍',
          icon: 'icon-xitonghanshu'
        },
        component: (resolve) => require(['../view/introduction/introduction.vue'], resolve)
      }
    ]
  }
]



