export default {
  state: {
    pageSize: 20,
    pageNumber: 1
  },
  mutations: {
    setPageSize (state, content) {
      state.pageSize = content
    },
    setPageNumber (state, content) {
      state.pageNumber = content
    }
  },
  actions: {
    actionPageSize (store, data) {
      store.commit('setPageSize', data)
    },
    actionPageNumber (store, data) {
      store.commit('setPageNumber', data)
    }
  }
}



