/* eslint-disable import/no-webpack-loader-syntax */
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueBus from 'vue-bus'
import { request } from 'common/axios.js'
import 'common/prototypeExtend.js'

require('!style-loader!css-loader!less-loader!css/theme.less')
require('!style-loader!css-loader!less-loader!css/mixins.less')
require('!style-loader!css-loader!less-loader!css/common.less')

Vue.config.productionTip = false // 关闭生产模式下给出的提示
Vue.prototype.$request = request // request请求方法

Vue.use(VueBus)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



